	props:["currName","currentTime","duration","isplay","percent"],
	filters:{
		secondTime(time){
			var minute = parseInt(time/60);
			minute = minute<10?'0'+minute:minute;
			var second = parseInt(time%60);
			second = second<10?'0'+second:second;
			return minute+':'+second;
		}
	},
